/* ==== Tech Hub IIT Tirupati BRAND NAME ==== */

.main-web-name {
  font-size: 180%;
  background-image: linear-gradient(
    90deg,
    rgb(0, 185, 214),
    rgb(3, 167, 177),
    rgb(3, 163, 137)
  );
  background-size: 100%;
  background-clip: text;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
}

/* ==== NAVIGATION LINKS ==== */

.navigation {
  box-sizing: border-box;
}

.navigation-link {
  display: inline-block;
  position: relative;
}

.navigation-link:link::before {
  content: '';
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  -webkit-transform: scaleX(0);
  -moz-transform: scaleX(0);
  -ms-transform: scaleX(0);
  -o-transform: scaleX(0);
  height: 2px;
  bottom: 0;
  left: 0;
  background: linear-gradient(90deg, rgba(0, 188, 242, 1), rgba(123, 163, 244, 1), rgba(180, 160, 255, 1)  );
  transform-origin: bottom right;
  transition: transform ease-in-out 0.5s;
  -webkit-transition: transform ease-in-out 0.5s;
  -moz-transition: transform ease-in-out 0.5s;
  -ms-transition: transform ease-in-out 0.5s;
  -o-transition: transform ease-in-out 0.5s;
}

.navigation-link:active::before,
.navigation-link:hover::before {
  transform: scaleX(1);
  -webkit-transform: scaleX(1);
  -moz-transform: scaleX(1);
  -ms-transform: scaleX(1);
  -o-transform: scaleX(1);
  transform-origin: bottom left;
}

.navbar-dark .navbar-toggler-icon {
