  border-radius: 10px;
  padding: 10px;
}

.event-card-content .event-card-content-overlay {
  background: rgba(32, 31, 30, 0.9);
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  opacity: 0;
  transition: all 0.4s ease-in-out 0s;
}

.event-card-content:hover .event-card-content-overlay {
  opacity: 1;
}

.event-card-content-image {
